@import url("https://fonts.googleapis.com/css?family=Righteous|Satisfy&display=swap");
.form-container {
  width: 40%;
  margin: 30%;
  margin-top: 0%;
  display: flex;
  flex-direction: column;
}

.form-text {
  align-self: center;
  text-align: center;
}

.input-container {
  height: 70px;
  width: 100%;
  background-color: #fff;
  margin: 1%;
  margin-left: 0%;
  margin-right: 0%;
  border-radius: 5px;
  border: none;
  border: 1px solid #db708a;
  outline: none;
}
.input-container:focus-within {
  border-style: solid;
  border: 1.3px solid #ff0000;
}
.create-account-button {
  margin: 2%;
  margin-top: 1%;
  margin-left: 0%;
  background: #db708a;
  border: "none";
  color: #fff;
  height: 70px;
  width: 100%;
  border-radius: 4px;
  box-shadow: 0px 0px 5px 0.5px #95a5a6;
  border-width: 0px;
}
.google-icon {
  position: absolute;
  margin: 0%;
  border: none;
  border-width: 0%;
  color: #fff;
  height: 60px;
  width: 4%;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  background-color: #e93a3a;
}
.create-account-button:focus {
  background: #e9a2b3;
  outline: none;
}
.input-clicked {
  border: none;
  align-self: center;
  position: relative;
  top: 30%;
  left: 4%;
  width: 95%;
}

.input-clicked:focus {
  border: none;
  outline: none;
}

.login-google-button-text {
  font-family: "Righteous", cursive;
  font-size: 17px;
  color: #fff;
}

.login-google-button {
  width: 100%;
  height: 100%;
  border: none;
  border-width: 0%;
  border-radius: 4px;
  background-color: #ff0000;
}

.social-container {
  display: inline-flex;
  font-size: 36px;
  justify-content: center;
}

.social-container i {
  margin-left: 5%;
}

.fab:hover {
  color: #4c6ef5;
  cursor: pointer;
}
