@import url(https://fonts.googleapis.com/css?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css?family=Righteous|Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
}

.NavBar {
  box-shadow: 0.1px 2px 1px #d4e1e7;
  border-radius: 1.4%;
  background: #fff;
}

.logo-text {
  font-family: "Satisfy", cursive;
  font-size: 150%;
  margin-left: 15%;
}

.create-button {
  background: #db708a;
  border: "none";
  color: #fff;
  height: 45px;
  border-radius: 4px;
  box-shadow: 0px 0px 5px 0.5px #dadfe1;
  border-width: 0px;
}

.nav-bar-right-text {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.whole-body {
  background-color: #e8f4fa;
  height: 570px;
}
.first-container {
  display: flex;
  justify-content: baseline;
}
.first-container-text {
  margin-left: 20%;
  align-self: flex-end;
  align-content: space-between;
}
.second-container {
  display: flex;
  margin-top: 10%;
  margin-left: 8%;
  justify-content: space-between;
}
.body-create-button {
  margin: 2%;
  margin-top: 8%;
  margin-left: 0%;
  background: #db708a;
  border: "none";
  color: #fff;
  height: 60px;
  width: 150px;
  border-radius: 4px;
  box-shadow: 0px 0px 5px 0.5px #95a5a6;
  border-width: 0px;
}

.mUD-4 {
  margin: 8%;
  margin-left: 0%;
  margin-right: 0%;
}

.form-container {
  width: 40%;
  margin: 30%;
  margin-top: 0%;
  display: flex;
  flex-direction: column;
}

.form-text {
  align-self: center;
  text-align: center;
}

.input-container {
  height: 70px;
  width: 100%;
  background-color: #fff;
  margin: 1%;
  margin-left: 0%;
  margin-right: 0%;
  border-radius: 5px;
  border: none;
  border: 1px solid #db708a;
  outline: none;
}
.input-container:focus-within {
  border-style: solid;
  border: 1.3px solid #ff0000;
}
.create-account-button {
  margin: 2%;
  margin-top: 1%;
  margin-left: 0%;
  background: #db708a;
  border: "none";
  color: #fff;
  height: 70px;
  width: 100%;
  border-radius: 4px;
  box-shadow: 0px 0px 5px 0.5px #95a5a6;
  border-width: 0px;
}
.google-icon {
  position: absolute;
  margin: 0%;
  border: none;
  border-width: 0%;
  color: #fff;
  height: 60px;
  width: 4%;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  background-color: #e93a3a;
}
.create-account-button:focus {
  background: #e9a2b3;
  outline: none;
}
.input-clicked {
  border: none;
  align-self: center;
  position: relative;
  top: 30%;
  left: 4%;
  width: 95%;
}

.input-clicked:focus {
  border: none;
  outline: none;
}

.login-google-button-text {
  font-family: "Righteous", cursive;
  font-size: 17px;
  color: #fff;
}

.login-google-button {
  width: 100%;
  height: 100%;
  border: none;
  border-width: 0%;
  border-radius: 4px;
  background-color: #ff0000;
}

.social-container {
  display: inline-flex;
  font-size: 36px;
  justify-content: center;
}

.social-container i {
  margin-left: 5%;
}

.fab:hover {
  color: #4c6ef5;
  cursor: pointer;
}

